<template>
    <v-container fluid style="height: 100vh;background-color: #FEE0DA;">
        <v-row class="ma-0 pa-0">
            <v-col align="start" class="ma-0 pa-0">
                <v-img alt="" contain src="@/images/linkpaymenttop.png" style="margin-top: 5px;"/>
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0">
            <v-col align="start" style="font-size: 11px;background-color: white;">
                <b>▶ HAWB:</b> {{ this.paymentInfo.gemMasterNo }}<br/>
                <b>▶ Sender:</b> {{ this.paymentInfo.senderName }}<br/>
                <b>▶ Recipient:</b> {{ this.paymentInfo.recipientName }}<br/>
                <b>▶ Destination:</b> {{ this.paymentInfo.recipientAddressCountryCode }} {{ this.paymentInfo.recipientAddressState }} {{ this.paymentInfo.recipientAddressCity }} {{ this.paymentInfo.recipientAddress1 }} {{ this.paymentInfo.recipientAddress2 }}<br/>
                <b>▶ Item:</b> {{ this.paymentInfo.mainItemName }}<br/>
                <b>▶ Box:</b> {{ this.paymentInfo.parcelCount }}<br/>
                <b>▶ Total Weight:</b> {{ this.paymentInfo.paymentWeight }} {{ this.paymentInfo.paymentWeightUnit }}<br/>
                <b>▶ Payment Details:</b><br/>
                &nbsp;&nbsp;&nbsp;<b>- Freight {{ this.paymentInfo.totalChargeAmount | comma }}</b><br/>
                <span v-for="item in paymentInfo.otherCharges" :key="item.id">
                &nbsp;&nbsp;&nbsp;<b>- {{ item.display }} {{ item.amount | comma }}</b><br/>
                </span>
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0">
            <v-col align="center" style="padding-left:20px">
                <v-btn v-if="this.paymentInfo.paymentNo == null || this.paymentInfo.paymentNo == ''" color="primary" @click="setPayment()" disabled>Error</v-btn>
                <v-btn v-else-if="this.paymentInfo.paymentStatus == 'COMPLETE'" color="primary" @click="setPayment()" disabled>결제가 완료되었습니다.</v-btn>
                <v-btn v-else-if="this.paymentInfo.paymentStatus != 'WAIT'" color="primary" @click="setPayment()" disabled>결제가 불가능합니다.</v-btn>
                <v-btn v-else color="primary" @click="setPayment()">{{ this.paymentInfo.paymentCurrency | currency }} {{ this.paymentInfo.paymentAmount | comma }} 결제하기</v-btn>
            </v-col>
        </v-row>
        <v-row class="ma-0 pa-0">
            <v-col align="center" style="padding-left:20px;font-size: 11px;">
                ㈜잼글로비스. 경기도 김포시 고촌읍 인향로210번길 24-25 (10131). 031-988-5277. info@gemglovis.com
            </v-col>
        </v-row>
        <!-- 로딩바 -->
        <loadingBarView :setLoadingBar="this.setLoadingBar"></loadingBarView>
        <!-- 로딩바 -->     
    </v-container>
</template>

<script>
import axios from "axios";
import common from '@/common'
import { loadTossPayments } from '@tosspayments/payment-sdk'
import loadingBarView from "@/components/LoadingBarView.vue"
import commonCode from "@/common/Code"

export default {
    components: {
        loadingBarView
    },      
    data: () => ({
        setLoadingBar : { loadingBarDialogShow : false },
        no : "",
        paymentInfo : "",
    }),
    methods: {
        getDataInit() {
            this.setLoadingBar.loadingBarDialogShow = true;
            axios.get(common.makeAPIUrl('/prework/link-payments?paymentNo=' + this.no)
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.paymentInfo = response.data;
                this.setLoadingBar.loadingBarDialogShow = false;

                if(this.paymentInfo.paymentAmount != Number(this.paymentInfo.totalChargeAmount) + Number(this.paymentInfo.totalOtherChargeAmount)) {
                    this.$alert("Payment amount check required.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }

                for(let index=0; index<this.paymentInfo.otherCharges.length; index++) {
                    this.paymentInfo.otherCharges[index].display = commonCode.getOtherChargeTypeDisplay(this.paymentInfo.otherCharges[index].name);
                }
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => { 
                    this.setLoadingBar.loadingBarDialogShow = false;
                 })
            })
        },
        setPayment() {
            var vm = this;
            var amount = this.paymentInfo.paymentAmount;
            var paymentNo = this.paymentInfo.paymentNo;
            var paymentId = this.paymentInfo.paymentId;
            var paymentName = this.paymentInfo.paymentName;
            var customerName = this.paymentInfo.recipientName;
            var successUrl = process.env.VUE_APP_AUTH_TOSS_SUCCESS_URL + "?paymentId=" + paymentId + "&paymentNo=" + paymentNo + "&paymentType=link";
            var failUrl = process.env.VUE_APP_AUTH_TOSS_FAIL_URL + "?paymentId=" + paymentId + "&paymentNo=" + paymentNo + "&paymentType=link";

            
            const clientKey = process.env.VUE_APP_AUTH_TOSS_CLIENT_KEY;
            loadTossPayments(clientKey).then(tossPayments => {
                tossPayments.requestPayment('카드', {
                    amount: amount,
                    orderId: paymentNo,
                    orderName: paymentName,
                    customerName: customerName,
                    successUrl: successUrl,
                    failUrl: failUrl,
                })
                .catch(function () {
                    var data = new Object();
                    data.errorCode = "";
                    data.errorMessage = "사용자에 의한 결제 취소";
                    data.paymentId = paymentId;

                    axios.post(common.makeAPIUrl('/prework/link-payments/fail'), data, { headers: {'Authorization': vm.$session.get('authorization')}})
                    .then(() => {
                        vm.setLoadingBar.loadingBarDialogShow = false;
                        vm.$alert(common.messages.paymentFailComplate, "", "info").then(() => {});
                        vm.getDataInit();
                    })
                    .catch((err) => {
                        vm.setLoadingBar.loadingBarDialogShow = false;
                        vm.$alert(err.response.data.message, "", "info").then(() => {});
                    });
                })
            });
        }
    },
    mounted() {
        var vm = this;
        this.no =  this.$route.query.no;

        if(this.no == null || this.no == "") {
            vm.$alert(common.messages.pageError, "", "info").then(() => {
                vm.setLoadingBar.loadingBarDialogShow = false;
                return false;
            });
        }
        this.getDataInit();
    },
}
</script>
